import {
	mapState,
	mapMutations
} from 'vuex';

import uniTag from '@/components/uni-tag/uni-tag.vue';
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'

export default {
	components: {
		uniTag,
		uniDrawer
	},
	data() {
		return {
			drawerMode: 0, //0:drawer close; 1: createClass drawer; 2: updateClass drawer;
			rpx: 0,
			currentClass: {
				weekday: "monday",
				name: "语文",
				time: 480,
				dur: 45,
				icon: "success",
				margintop: 0
			},
			show: {
				middle: false,
				top: false,
				bottom: false,
				right: false,
				right2: false
			}
		}
	},
	methods: {
		...mapMutations(['increaseCount', 'increaseCountBy', 'decreaseCount', 'updateProjs', 'addClass']),
		// onUpdateClassName: function(e) {
		// 	this.classname = e.detail.value;
		// },
		// onChangeWeekday: function(day) {
		// 	this.weekday = day;
		// },
		// bindTimeChange: function(e) {
		// 	this.cuclassStartTime = e.target.value;
		// },
		getStorage: function() {
			var key = this.key;
			//     data = this.projs;
			if (key.length === 0) {
				uni.showModal({
					title: '读取数据失败',
					content: "key 不能为空",
					showCancel: false
				})
			} else {
				uni.getStorage({
					key: key,
					success: (res) => {
						// console.log(JSON.stringify(res.data));
						// uni.showModal({
						//     title: '读取数据成功',
						//     content: "data: '" + res.data + "'",
						//     showCancel: false
						// })
						this.projs = res.data;
					},
					fail: () => {
						uni.showModal({
							title: '读取数据失败',
							content: "找不到 key 对应的数据",
							showCancel: false
						})
					}
				})
			}
		},
		setStorage: function() {
			var key = this.key
			var data = this.projs
			if (key.length === 0) {
				uni.showModal({
					title: '保存数据失败',
					content: 'key 不能为空',
					showCancel: false
				})
			} else {
				uni.setStorage({
					key: key,
					data: data,
					success: (res) => {
						uni.showModal({
							title: '存储数据成功',
							content: ' ',
							showCancel: false
						})
					},
					fail: () => {
						uni.showModal({
							title: '储存数据失败!',
							showCancel: false
						})
					}
				})
			}
		},
		clearStorage: function() {
			uni.clearStorageSync()
			this.key = '',
				this.data = ''
			uni.showModal({
				title: '清除数据成功',
				content: ' ',
				showCancel: false
			})
		},
		dirto: function() {
			console.log('dirto');
			uni.navigateTo({
				url: '/pages/viewtest/viewtest'
			});
		},
		formatDate: function(date) {
			var h = parseInt(date / 60);
			var s = date % 60;
			s = s + '';
			// console.log('second:' + s + ', second length:' + s.length)
			if (s.length == 1)
				s = '0' + s;
			return [h, s].join(':');
		},
		onEditClass(e) {
			console.log(JSON.stringify(e.currentTarget.dataset));
			this.e = e;
			this.currentClass = e.currentTarget.dataset.class;
			//this.toggle('right');
			this.drawerMode = 1;
		},
		toggle(type) {
			console.log(type);
			this.show[type] = !this.show[type];
		},
		submitUpdateClass: function() {
			console.log('submitUpdateClas');
			this.updateClass(currentClass, x, y);
			this.cancelCreateProj();
		},
		submitAddClass: function() {
			console.log('submitCreateProj');
			this.addClass({
				weekday: this.weekday,
				name: this.classname,
				time: this.classStartTime,
				dur: this.classDur
			});
			this.cancelCreateProj();
		},
		cancelCreateProj: function() {
			this.show['right'] = false;
		},
	},
	computed: {
		projs: {
			get: function() {
				console.log('projs get')
				return this.$store.state.projs
			},
			set: function(val) {
				console.log('projs set')
				this.$store.commit('setProjs', val)
			}
		}
	},
	onLoad() {
		// 获取系统信息
		uni.getSystemInfo({
			success: res => {
				this.systemInfo = res;
				this.rpx = res.windowHeight / 600;
			}
		});
		//this.loadData();
		//this.getStorage();
		this.updateProjs(this.rpx);
	}
}
